import './src/styles/index.scss';
const scrollTo = (id) => () => {
  const el = document.getElementsByName(id)[0]
  if (el) return window.scrollTo(0, el.offsetTop - 20)
  return false
}

export const onRouteUpdate = ({ location: { hash } }) => {
  if (hash) {
    window.setTimeout(scrollTo(hash.replace('#', '')), 10)
  }
}