import React from "react"
import { StaticQuery, graphql, Link } from "gatsby"
import * as scss from "./ReposByTag.module.scss"
import Tags from "../data/tags.json"

const getTagIndex = (tagName) => {
  const res = Tags.findIndex((t) => t.key === tagName)

  if (res === -1) return 999

  return res
}

const getTagByKey = (tagName) => {
  return Tags.find((t) => t.key === tagName) || false
}

const sortGroupByTag = (a, b) => {
  const idxA = getTagIndex(a.fieldValue)
  const idxB = getTagIndex(b.fieldValue)

  if (idxA > idxB) {
    return 1;
  }
  if (idxA < idxB) {
    return -1;
  }
  return 0;
}

const sortTags = (a, b) => {
  const idxA = getTagIndex(a)
  const idxB = getTagIndex(b)

  if (idxA > idxB) {
    return 1;
  }
  if (idxA < idxB) {
    return -1;
  }
  return 0;
}

const renderFunction = (data) => {
  const items = []
  const tags = data.allMdx.group

  for (const [index, value] of tags.sort((a, b) => sortGroupByTag(a, b)).entries()) {
    items.push(<div key={index}>{renderTag(value)}</div>)
  }

  return <ul>{items}</ul>
}

const renderTag = (data) => {
  const items = []
  const repos = data.edges

  items.push(<h3 name={data.fieldValue} className={scss.block}>{getTagByKey(data.fieldValue).name || data.fieldValue}</h3>)
  for (const value of repos) {
    items.push(renderRepo(value.node))
  }
  return <li className={scss.flex}>{items}</li>
}

const renderTagList = (tags) => {
  const items = []

  for (const t of tags.sort((a, b) => sortTags(a, b))) {
    let tagClass = "bx--tag"
    if (getTagByKey(t)) {
      tagClass += " bx--tag--green"
    } else {
      tagClass += " bx--tag--cool-gray"
    }
    items.push(<button class={tagClass}> <span class="bx--tag__label" title={getTagByKey(t).name}>{t}</span> </button>)
  }

  return items
}

const renderRepo = (data) => {
  
  const fm = data.frontmatter
  const slug = data.slug
  const item = (
    <div>
      <div className={scss.pb}>
        <h4>{fm.title}</h4>
        <p className={scss.italic}>{fm.description}</p>
      </div>
      <p className={scss.pt}>{renderTagList(fm.tags)}</p>
    </div>
  )

  return <Link to={slug} className={scss.tile}>{item}</Link>
}

const ReposByTag = () => (
  <StaticQuery
    query={graphql`
    {
      allMdx(
        filter: {fields: {source: {eq: "toolkit"}},
                 slug: {glob: "*"}}
        sort: {fields: frontmatter___weight, order: ASC}
      ) {
        group(field: frontmatter___tags) {
          edges {
            node {
              id
              frontmatter {
                description
                title
                tags
                url
              }
              slug
            }
          }
          fieldValue
        }
      }
    }
    `}
    render={data => renderFunction(data)}
  />
)

export default ReposByTag



