import React from "react"
import { StaticQuery, graphql, Link } from "gatsby"
import * as scss from "./BlogPost.module.scss"

const renderFunction = (data) => {
    const items = []
    const edges = data.allMdx.edges

    for (const [index, value] of edges.entries()) {
        items.push(<div key={index}>{renderPost(value.node)}</div>)
    }
    return <ul>{items}</ul>
}

const renderPost = (data) => {
  
    const fm = data.frontmatter
    const slug = data.slug
    const item = (
      <li>
        <div className={scss.pb}>
          <h4>{fm.title}</h4>
          <p className={scss.italic}>{fm.description}</p>
          <p className={scss.pt}>{fm.date}</p>
        </div>
      </li>
    )
  
    return <Link to={slug} className={scss.tile}>{item}</Link>
}

const BlogPosts = () => (
  <StaticQuery
  query={graphql`
  {
    allMdx(
      filter: {fields: {source: {eq: "blog"}}}
      sort: {fields: frontmatter___date, order: DESC}
    ) {
      edges {
        node {
          id
          frontmatter {
            weight
            title
            description
            date
          }
          slug
        }
      }
    }
  }
`}
    render={data => renderFunction(data)}
  ></StaticQuery>
)

export default BlogPosts

